.block {
  width: 100%;
  background: red;
  height: 100px;
  border: 2px solid yellow;
  position: relative; }
  .block:after {
    content: "Block!";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.block-wrapper {
  padding: 10px; }
